@font-face {
  font-family: 'Poppins-Light';
  src: url("../../src/fonts/Poppins-Light.ttf") format("truetype"); }

@font-face {
  font-family: 'Poppins-Bold';
  src: url("../../src/fonts/Poppins-Bold.ttf") format("truetype"); }

@font-face {
  font-family: 'Poppins-Regular';
  src: url("../../src/fonts/Poppins-Regular.ttf") format("truetype"); }

* {
  box-sizing: border-box; }

.container {
  max-width: 1240px;
  margin: auto; }

header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  header .main-nav {
    background-color: #545454; }
    header .main-nav__list {
      display: flex;
      justify-content: center; }
    header .main-nav__item {
      font-family: 'Roboto', sans-serif;
      padding: 10px 20px 10px 20px;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 19px;
      color: white;
      cursor: pointer;
      transition: 0.3s; }
      header .main-nav__item:hover {
        background-color: #F4902F; }
      header .main-nav__item a {
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
        color: white;
        text-decoration: none; }
      header .main-nav__item:first-child {
        margin-left: -20px; }
  header .contact-section {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 0; }
    header .contact-section__logo {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: flex-start; }
    header .contact-section__btn {
      background-color: #23C4D2;
      border-radius: 100px;
      padding: 12px 20px;
      margin-right: 20px;
      cursor: pointer;
      transition: 0.3s; }
      header .contact-section__btn:hover {
        background-color: #F4902F;
        filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25)); }
      header .contact-section__btn span {
        color: white;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 27px; }
    header .contact-section__contacts {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column; }
      header .contact-section__contacts a {
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 21px;
        text-decoration: none;
        color: black;
        transition: 0.3s; }
        header .contact-section__contacts a:hover {
          opacity: 0.7; }

.header-slider {
  width: 100%;
  height: auto; }
  .header-slider__item {
    height: auto;
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: contain;
    padding: 50px 0 50px 0; }
  .header-slider__title {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 60px;
    margin-bottom: 20px; }
  .header-slider__desc {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    padding-right: 34%;
    margin-bottom: 100px; }
  .header-slider__more {
    background-color: #F4902F;
    border-radius: 100px;
    padding: 12px 20px;
    cursor: pointer;
    color: #fff;
    font-family: Poppins,sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    margin-left: calc((100% - 1240px) / 2);
    transition: 0.3s; }
    .header-slider__more:hover {
      background-color: #23C4D2;
      filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25)); }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0); }

.slick-track:before, .slick-track:after {
  content: "";
  display: table; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: block; }
